.container {
  width: 90%;
  max-width: 700px;
  margin: 0 auto;
  padding-top: 70px;
  padding-bottom: 70px;

  .head-row {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    div {
      &:nth-child(1) {
        max-width: 30%;
        img {
          //height: 50%;
          width: 80%;
        }
      }

      &:nth-child(2) {
        width: 70%;
        text-align: center;
        font-size: 1.4em;
        font-weight: bold;
        letter-spacing: 2px;
        line-height: 60px;
      }
    }
  }

  .handle-row {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    column-gap: 10px;
    padding-top: 10px;

    & > div {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      row-gap: 5px;

      &:nth-child(1),
      &:nth-child(2) {
        border-right: 1px solid rgba(128, 127, 127, 0.272);
        text-align: left;
        padding-left: 5px;
        padding-right: 10px;
      }

      div {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        column-gap: 7px;

        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
        }

        div {
          line-height: 30px;
        }
      }
    }
  }

  .toolbar {
    margin-top: 25px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    span {
      &:nth-child(5) {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;

        div {
          &:nth-child(2) {
            width: 90px;
            height: auto;
            border-bottom: 1px dashed #222;
          }
        }
      }
    }

    .span-spacer-one {
      flex: 1 auto;
    }
  }

  .details {
    & > div {
      margin-top: 30px;

      &:nth-child(2) {
        border-radius: 5px;
        border: 1px solid rgba(49, 48, 48, 0.478);

        h2 {
          padding: 15px;
        }

        & > div {
          padding: 15px;
        }
      }

      h2 {
        font-size: 1.4em;
      }

      & > div {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        row-gap: 1px;

        & > div {
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          // background: blue;
          margin-top: 1px;

          p {
            &:nth-child(1) {
              min-width: 100px;
            }
          }

          div {
            border-bottom: 1px dashed #222;
            width: 100vw;
            height: 27px;
            // where
          }
        }
      }
    }
  }

  .price {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    height: 40px;
    padding-top: 30px;

    h3 {
      width: 110px;
    }

    div {
      width: 40%;
      height: 40px;
      border: 1px solid rgba(82, 79, 79, 0.723);
    }
  }

  .foot-salute {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    column-gap: 20px;
    margin-top: 60px;

    h4 {
      font-size: 1.3em;
    }

    & > div {
      width: 50%;
      & > div {
        & > div {
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;

          div {
            border-bottom: 1px dashed #222;
            width: 70%;
            height: 27px;
          }
        }
      }
    }
  }

  .terms-and-c {
    padding-top: 50px;
  }
}

.contaion-actual-word {
  padding-left: 10px;
}

.contaion-actual-price {
  padding-left: 20px;
  padding: 0;
  line-height: 50px;
}

.actions-div {
  background: rgb(211, 211, 211);
  border-radius: 7px;
  padding: 10px;
  height: 80px;
  width: 100%;
  margin-bottom: 10px;
}

@media print {
  .actions-div {
    display: none;
  }
}

.actions-txt-divi {
  font-size: 1.5em;
}
